import './bootstrap.min.css';
import './App.css';

import { useState } from 'react';
import React from 'react';

import Cookies from 'universal-cookie';

const cookies = new Cookies();

function App() {
  let maxT = cookies.get('max')
  if (maxT === undefined){
    maxT = 100
  }

  let minT = cookies.get('min')
  if (minT === undefined){
    minT = 10
  }

  const defaultMax = maxT
  const defaultMin = minT

  const getRandomInt = (min, max)=> {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    return Math.round(rand);
  }

  let [first, setFirst] = useState(getRandomInt(defaultMin, defaultMax))
  let [second, setSecond] = useState(getRandomInt(defaultMin, defaultMax))

  let [max, setMax] = useState(defaultMax)
  let [min, setMin] = useState(defaultMin)

  let [res, setRes] = useState('')
  let [resText, setResText] = useState('')

  let d1 = parseInt(cookies.get('score'))
  if (isNaN(d1)){
    d1 = 0
  }

  let [score, setScore] = useState(d1)

  let op = ''
  let d = getRandomInt(1, 3)
  if (d === 1){
    op = '+'
  } else if (d === 2){
    op = '-'
  } else if (d === 3){
    op = '*'
  }

  let [operation, setOperation] = useState(op)

  const generate = ()=>{
    setFirst(getRandomInt(min, max))
    setSecond(getRandomInt(min, max))
    let d = getRandomInt(1, 3)
    if (d === 1){
      setOperation('+')
    } else if (d === 2){
      setOperation('-')
    } else if (d === 3){
      setOperation('*')
    }
  }

  let answer = React.createRef()

  const submitAnswer = ()=>{
    const s = first.toString()+operation+second.toString()
    const ans = eval(s)
    if (ans == answer.current.value){
      let p = cookies.get('score')
      if (p !== undefined){
        p = parseInt(p)
        if (!isNaN(p)){
          cookies.set('score', p+1)
          setScore(p+1)
        } else {
          cookies.set('score', 1)
          setScore(1)
        }
      } else {
        cookies.set('score', 1)
        setScore(1)
      }
      setRes('blue')
      setResText('Correct. '+first.toString()+' '+operation+' '+second.toString()+' = '+ans)
    } else {
      setRes('red')
      setResText('Incorrect. '+first.toString()+' '+operation+' '+second.toString()+' = '+ans)
    }
    answer.current.value = ''
    generate()
  }

  return (
    <div className="App">
      <center>
        <h1>Verbal count</h1>
        <br/>
        <br/>
        <div>
          <h2>
            <p style={{color: 'blue'}}>Your score is {score}</p>
            <br/>
            <div>{first}&nbsp;{operation}&nbsp;{second}&nbsp;=</div>&nbsp;
            <br/>
            <input ref={answer} style={{marginLeft: '10px', width: '70%', fontSize: '20px'}} className="form-control" type="number" />
            {res !== '' ? <>
              <br/>
              <p style={{color: res}}>{resText}</p>
            </> : <></>}
          </h2>
          <br/>
          <br/>
          <br/>
          <br/>
          <p className="btn btn-info" onClick={submitAnswer} style={{cursor: 'pointer'}}>Submit</p>
          <br/>
          <br/>
          <p onClick={generate} className="btn btn-warning" style={{cursor: 'pointer'}}>Generate</p>
          <br/>
          <br/>
          <br/>
          <div>
              <h2>Max:</h2>
              <input value={defaultMax} onChange={(e)=>{
                cookies.set('max', e.target.value)
                setMax(e.target.value)
              }} style={{width: '60%'}} type="number" className="form-control"/>
              <br/>
              <br/>
              <h2>Min:</h2>
              <input value={defaultMin} onChange={(e)=>{
                cookies.set('min', e.target.value)
                setMin(e.target.value)
              }} style={{width: '60%'}} type="number" className="form-control"/>
              <br/>
              <br/>
              <br/>
            </div>
        </div>
      </center>
    </div>
  );
}

export default App;
